
import {
	login
} from './utils/login.js'
import {
	getConfig
} from './api/api.js'
export default {
	globalData: {

	},
	onLaunch: function() {
		console.log('App Launch')
		this.$state.setState({
			userAuth: false
		})
		this.getConfig();
		login()
			.then(r => {
				this.$state.userAuth = r;
			})
	},
	onShow: function() {
		console.log('App Show')
	},
	onHide: function() {
		console.log('App Hide')
	},
	methods: {
		getConfig() {
			getConfig({
					wxCloud: true
				})
				.then(r => {
					getApp().globalData.lessTime = r.result.lessTime;
					if (getApp().configCB) getApp().configCB(r.result);
				})
		}
	}
}
